import axios from 'axios';
import  { userBaseUrl, baseUrl } from "api/baseApi"

// Login Method()
export const loginAPI = async ({ account, password, role }) => {
  // role or
  const result = await axios.post(`${baseUrl}/${role}/signin`, {
    account,
    password
  }).then((res) => {
    return res;
  }).catch((err => {
    console.log(err)
    return err;
  }))

  // Component
  return result;
};


// Register Method()
export const RegisterAPI = async ({ req_data }) => {
  const result = await axios.post(userBaseUrl, {
    account: req_data.account,
    name: req_data.name,
    email: req_data.email,
    password: req_data.password,
    checkPassword: req_data.confirm_password
  }).then((res) => {
    return res;
  }).catch((err => {
    console.log(err)
    return err;
  }))

  // Component
  return result;
};

// 
export const checkLoginStatusAPI = async (authToken) => {
  try {
    const response = await axios.get(`${userBaseUrl}/token`, {
      headers: {
        Authorization: `Bearer ${authToken}`
      },
    });
    return response.status === 200;
  } catch (error) {
    console.error('[Check Permission Failed]:', error);
  }
};