import { useEffect, useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { RegisterAPI } from "api/auth";
import { editAccountAPI } from "api/main";
import "styles/auth_form.css";
import { useAuth } from "context/AuthContext";
import Notification from "components/Form/Notification";
import { useNoti } from "context/NotiContext";

// email 
const email_rule =
  /^\w+((-\w+)|(.\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z]+$/;

export default function AccountForm({ userData, current_page }) {
  const navigate = useNavigate();
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirm_password, setConfirmPassword] = useState("");
  const [errorMessage, setErrorMessate] = useState(["", ""]);
  const { currentMember } = useAuth();
  const { is_alert, setIsAlert, setNotiMessage } = useNoti();

  useEffect(() => {
    // Setting 
    if (userData !== undefined && current_page === "setting") {
      setAccount(userData.account);
      setName(userData.name);
      setEmail(userData.email);
    }
  }, [userData, current_page]);

  function accountChange(value) {
    setErrorMessate(["", ""]);
    setAccount(value);
    if (account.length >50) {
      setNotiMessage({type:"error", message:"50"});
      setErrorMessate(["account", "50"]);
      return false;
    }
  }

  function nameChange(value) {
    setErrorMessate(["", ""]);
    setName(value);
    if (value.length > 50) {
      setErrorMessate(["name", "50"]);
    }
  }

  function emailChange(value) {
    setErrorMessate(["", ""]);
    setEmail(value);
  }

  function passwordChange(value) {
    setErrorMessate(["", ""]);
    setPassword(value);
  }

  function confirmPasswordChange(value) {
    setErrorMessate(["", ""]);
    setConfirmPassword(value);
  }

  // 
  function checkData() {
    if (account.length === 0) {
      setNotiMessage({ type: "error", message: "" });
      setErrorMessate(["account", ""]);
      return false;
    }
    if (account.length >50) {
      setNotiMessage({type:"error", message:"50"});
      setErrorMessate(["account", "50"]);
      return false;
    }
    if (name.length === 0) {
      setErrorMessate(["name", ""]);
      setNotiMessage({ type: "error", message: "" });
      return false;
    }
    if (name.length > 50) {
      setErrorMessate(["name", "50"]);
      setNotiMessage({ type: "warning", message: "50" });
      return false;
    }
    if (email.length === 0) {
      setErrorMessate(["email", "Email"]);
      setNotiMessage({ type: "error", message: "Email" });
      return false;
    }
    if (email.search(email_rule) === -1) {
      setErrorMessate(["email", "Email"]);
      setNotiMessage({ type: "error", message: "Email" });
      return false;
    }
    if (password.length === 0) {
      setErrorMessate(["password", ""]);
      setNotiMessage({ type: "error", message: "" });
      return false;
    }
    if (password.length <5 || password.length > 12) {
      setErrorMessate(["password", "5~12"]);
      setNotiMessage({type:"error", message:"5~12"});
      return false;
    }
    if (confirm_password.length === 0) {
      setErrorMessate(["confirm_password", ""]);
      setNotiMessage({ type: "error", message: "" });
      return false;
    }
    if (confirm_password !== password) {
      setErrorMessate(["confirm_password", ""]);
      setNotiMessage({ type: "error", message: "" });
      return false;
    }
    return true;
  }

  //  (RegisterPage)
  async function Register() {
    setErrorMessate(["", ""]);
    const is_check = checkData();
    if (is_check) {
      const req_data = { account, name, email, password, confirm_password };
      // fetch API 
      const result = await RegisterAPI({ req_data });
      // 
      if (result.status === 200) {
        setNotiMessage({type:"success", message:""});
        setIsAlert(true);
        // 
        navigate("/login");
      } else {
        if (result.response.data.message === "Existing email or user account") {
          setNotiMessage({type:"error", message:"Email"});
          setIsAlert(true);
        }
        if (result.response.data.message === "Confirm password is incorrect") {
          setNotiMessage({type:"error", message:""});
          setIsAlert(true);
        }
        if (
          result.response.data.message ===
          "Password length must be between 5 and 12 characters"
        ) {
          setNotiMessage({type:"error", message:"5~12"});
          setIsAlert(true);
        }
        setIsAlert(true);
        return;
      }
    }
    else{
      setIsAlert(true);
    }
  }

  //  (SettingPage)
  async function Setting() {
    setErrorMessate(["", ""]);
    const is_check = checkData();
    if (is_check) {
      const req_data = { account, name, email, password, confirm_password };
      // fetch API 
      const result = await editAccountAPI(currentMember.id, req_data);
      // 
      if (result.status === 200) {
        const new_data = result.data.data.user;
        setNotiMessage({type:"success", message:""});
        setIsAlert(true);
        setPassword('')
        setConfirmPassword('')
        setAccount(new_data.account)
        setName(new_data.name)
        setEmail(new_data.email)
       
      } else {
        if (result.response.data.message === "Existing email or user account") {
          setNotiMessage({type:"error", message:"Email"});
          setIsAlert(true);
        }
        if (result.response.data.message === "Confirm password is incorrect") {
          setNotiMessage({type:"error", message:""});
          setIsAlert(true);
        }
        if (
          result.response.data.message ===
          "Password length must be between 5 and 12 characters"
        ) {
          setNotiMessage({type:"error", message:"5~12"});
          setIsAlert(true);
        }
        setIsAlert(true);
        return;
      }
    }
    else{
      setIsAlert(true);
    }
  }

  // KeyDown 
  function handleKeyDown(key) {
    if (key === "Enter") {
      current_page === "register" ? Register() : Setting();
    }
  }

  return (
    <div className="account_form">
      <div className="input_group">
        {/* Account */}
        <FormInput
          data={{
            title: "",
            name: "account",
            type: "text",
            placeholder: "",
          }}
          onChange={accountChange}
          onKeyDown={handleKeyDown}
          value={account}
          err_msg={errorMessage}
        />

        {/* Name */}
        <FormInput
          data={{
            title: "",
            name: "name",
            type: "text",
            placeholder: "",
          }}
          onChange={nameChange}
          onKeyDown={handleKeyDown}
          value={name}
          err_msg={errorMessage}
        />

        {/* Email */}
        <FormInput
          data={{
            title: "Email",
            name: "email",
            type: "email",
            placeholder: "Email",
          }}
          onChange={emailChange}
          onKeyDown={handleKeyDown}
          value={email}
          err_msg={errorMessage}
        />

        {/* Passowrd */}
        <FormInput
          data={{
            title: "",
            name: "password",
            type: "password",
            placeholder: "",
          }}
          onChange={passwordChange}
          onKeyDown={handleKeyDown}
          value={password}
          err_msg={errorMessage}
        />

        {/* Confirm Password */}
        <FormInput
          data={{
            title: "",
            name: "confirm_password",
            type: "password",
            placeholder: "",
          }}
          onChange={confirmPasswordChange}
          onKeyDown={handleKeyDown}
          value={confirm_password}
          err_msg={errorMessage}
        />
      </div>

      {current_page === "register" ? (
        <div className="register_btn_div">
          <button className="submit_btn" onClick={Register}>
            
          </button>
          <NavLink to="/login">
            <button className="cancel_btn"></button>
          </NavLink>
        </div>
      ) : (
        <div className="setting_btn_div">
          <button className="edit_btn" onClick={Setting}>
            
          </button>
        </div>
      )}

      {/* */}
      {is_alert && <Notification />}
    </div>
  );
}

function FormInput({ data, onChange, onKeyDown, value, err_msg }) {
  let input_style = "input"; //input
  let message = ""; //

  if (err_msg[0] === data.name) {
    input_style = "input_error"; //error
    message = <label className="error_message">{err_msg[1]}</label>;
  }

  return (
    <div className="input_div">
      <label htmlFor={data.title}> {data.title}</label>
      <input
        className={input_style}
        id={data.title}
        name={data.name}
        type={data.type}
        placeholder={data.placeholder}
        onChange={(e) => {
          onChange(e.target.value);
        }}
        onKeyDown={(e) => {
          onKeyDown(e.key);
        }}
        value={value}
      />
      {/*  */}
      {message}
    </div>
  );
}
