import { useEffect, useState } from "react";
import { getUserDataAPI, editUserDataAPI } from "api/userProfile";
import { useAuth } from "context/AuthContext";
import { useNoti } from "context/NotiContext";

import default_user_cover from "assets/images/default_user_cover.jpg";

// style
import "styles/profileModal.css";
// svg
import { ReactComponent as IconClose } from "assets/icons/close.svg";
import { ReactComponent as IconAddPhoto } from "assets/icons/addphoto.svg";

export default function ReplyModal({ onModalToggle }) {
  const [name, setName] = useState("");
  const [introduction, setIntroduction] = useState("");
  const [avatar, setAvatar] = useState(null); // avatar
  const [avatarFile, setAvatarFile] = useState(null); //avatar
  const [cover, setCover] = useState(null); // cover
  const [coverFile, setCoverFile] = useState(null); //cover
  const [resetCover, setResetCover] = useState(false); //cover
  const { currentMember } = useAuth();
  const { setIsAlert, setNotiMessage } = useNoti();

  // 
  useEffect(() => {
    async function getUserData() {
      const result = await getUserDataAPI(currentMember.id);
      if (result.status === 200) {
        setName(result.data.name);
        setIntroduction(result.data.introduction);
        setAvatar(result.data.avatar);
        setCover(result.data.cover);
      }
    }
    getUserData();
  }, [currentMember]);

  // avatar
  function handleAvatar(event) {
    if (event.target.files[0] !== undefined) {
      setAvatar(URL.createObjectURL(event.target.files[0])); //
      setAvatarFile(event.target.files[0]);
    }
  }

  // cover
  function handleCover(event) {
    if (event.target.files[0] !== undefined) {
      setCover(URL.createObjectURL(event.target.files[0])); //
      setCoverFile(event.target.files[0]);
      setResetCover(false);
    }
  }

  // cover
  function handleReset() {
    let reset = window.confirm("");
    if (reset === true) {
      setResetCover(true);
      setCover(default_user_cover);
    } else {
    }
  }

  //  User Data
  async function editUserData() {
    // 
    if (name.length === 0) {
      setNotiMessage({ type: "error", message: "" });
      setIsAlert(true);
      return;
    }
    if (name.length > 50) {
      setNotiMessage({ type: "error", message: "50" });
      setIsAlert(true);
      return;
    }
    if (introduction.length === 0) {
      setNotiMessage({ type: "error", message: "" });
      setIsAlert(true);
      return;
    }
    if (introduction.length > 50) {
      setNotiMessage({ type: "error", message: "50" });
      setIsAlert(true);
      return;
    }

    const data = {
      name,
      introduction,
      avatar: avatarFile,
      cover: coverFile,
      reset: resetCover,
    };
    const result = await editUserDataAPI(currentMember.id, data);
    if (result.status === 200) {
      setNotiMessage({ type: "success", message: "" });
      setIsAlert(true);
      onModalToggle(false, true);
    }
  }

  return (
    <div className="reply_modal">
      <div
        className="gray_panel"
        onClick={() => {
          onModalToggle(false, false);
        }}
      ></div>
      <div className="modal_panel">
        <div className="modal_header">
          <div className="header-left">
            <span
              className="close_btn"
              onClick={() => {
                onModalToggle(false, false);
              }}
            >
              <IconClose />
            </span>
            <h5></h5>
          </div>

          <button
            type="button"
            className="btn-save"
            onClick={() => {
              editUserData();
            }}
          >
            
          </button>
        </div>
        <div className="modal_body">
          <div className="modal-cover-image">
            <img
              src={cover}
              alt="user cover img-gray-panel"
              className="user_cover"
            />
            <span className="cover-gray-panel">
              <div className="cover-edit-setting">
                <label className="svg-label" htmlFor="upload_cover">
                  <IconAddPhoto className="add-cover setting-icon-svg add-photo-icon" />
                </label>
                <input
                  className="upload-input"
                  type="file"
                  name=""
                  id="upload_cover"
                  onChange={handleCover}
                />
                <label className="svg-label" onClick={handleReset}>
                  <IconClose className="delete-cover setting-icon-svg" />
                </label>
              </div>
            </span>
          </div>
          <div className="modal-avatar-wrapper">
            <img
              src={avatar}
              alt="user avatar"
              className=" modal-avatar-img avatar-container"
            />
            <span className="avatar-gray-panel">
              <label htmlFor="upload_avatar" className="avatar-edit-setting">
                <IconAddPhoto className="setting-icon-svg add-photo-icon" />
              </label>
              <input
                className="upload-input"
                type="file"
                name="avatar"
                id="upload_avatar"
                onChange={handleAvatar}
              />
            </span>
          </div>
          <div className="edit-profile-container">
            <form className="form profile-info-form">
              <div className="input_group">
                <div className="input_div">
                  <label htmlFor="name_input"></label>
                  <input
                    id="name_input"
                    name="name"
                    type="text"
                    placeholder=""
                    value={name}
                    onChange={(e) => {
                      setName(e.target.value);
                    }}
                  />
                </div>
                <div className="input_div">
                  <label htmlFor="introduction_input"></label>
                  <textarea
                    id="introduction_input"
                    name="introduction"
                    placeholder=""
                    value={introduction}
                    onChange={(e) => {
                      setIntroduction(e.target.value);
                    }}
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
