import { createContext, useContext, useState } from "react";

const defaultValue = {
  is_alert: false,
  noti_message: {type:'',message:''},
  activeNavItem: ''
};
// noti_message: {type:'success',message:''}
// type: 'success' / 'error' / 'warning' / 'info'

const NotiContext = createContext(defaultValue);
export const useNoti = () => useContext(NotiContext);

export const NotiProvider = ({ children }) => {
  const [is_alert, setIsAlert] = useState(false); //
  const [noti_message, setNotiMessage] = useState({type:'',message:''}); //
  const [activeItem, setActiveItem] = useState("main"); //NavbarItem 
  
  return (
    <NotiContext.Provider 
      value={{
          is_alert,
          setIsAlert,
          noti_message,
          setNotiMessage, 
          activeItem,
          setActiveItem
        }}
      >
      {children}
    </NotiContext.Provider>
  );
};
