import axios from 'axios';
import { baseUrl } from 'api/baseApi.js'

const axiosInstance = axios.create({
    baseUrl: baseUrl,
});

// Check AuthToken
axiosInstance.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem("AuthToken");
    if (token) {
      config.headers["Authorization"] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    console.error(error);
  }
);

// Get TweetList (-)
export const getTweetListAPI = async () => {
  try {
    const response = await axiosInstance.get(`${baseUrl}/tweets`);
    return response;
  } catch (error) { 
    console.log(error)
    return error
  }
};

// Get User TweetList (-)
export const getUserTweetListAPI = async (id) => {
  try {
    const response = await axiosInstance.get(`${baseUrl}/users/${id}/tweets`);
    return response;
  } catch (error) { 
    console.log(error)
    return error
  }
};

