// Edit User Account ()
export const editAccountAPI = async (id, data) => {
  try {
    const response = await axiosInstance.put(`${baseUrl}/users/${id}/setting`,{
      account: data.account,
      name: data.name,
      email: data.email,
      password: data.password,
    });
    return response;
  } catch (error) { 
    console.log(error)
    return error
  }
};

