import { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import "styles/auth_form.css";
import { loginAPI } from "api/auth";
import { useAuth } from "context/AuthContext";
import { useNoti } from "context/NotiContext";
import Notification from "components/Form/Notification";

export default function LoginForm({ current_page }) {
  const navigate = useNavigate();
  const { setLoginState } = useAuth();
  const [account, setAccount] = useState(""); //
  const [password, setPassword] = useState(""); //
  const [errorMessage, setErrorMessage] = useState(["", ""]); //input
  const { is_alert, setIsAlert, setNotiMessage } = useNoti();

  function accountChange(value) {
    setAccount(value);
  }

  function passwordChange(value) {
    setPassword(value);
  }

  // 
  async function Login() {
    setErrorMessage(["", ""]);
    // 
    if (account.length <= 0) {
      setErrorMessage(["account", ""]);
      return;
    }
    if (password.length <= 0) {
      setErrorMessage(["password", ""]);
      return;
    }

    // fetch Login API
    const result = await loginAPI({ account, password, role: current_page });
    if (result.status === 200) {
      localStorage.setItem("AuthToken", result.data.data.token);
      setLoginState(true);
      setNotiMessage({ type: "success", message: "" });
      setIsAlert(true);
      current_page === "users" ? navigate("/main") : navigate("/admin");
    } else {
      setLoginState(false);
      setIsAlert(true); //
      if (result.response.status === 400) {
        setErrorMessage(["password", "5~12"]);
        setNotiMessage({ type: "error", message: "5~12" });
      } else if (result.response.status === 401) {
        setErrorMessage(["password", ""]);
        setNotiMessage({ type: "error", message: "" });
      } else if (result.response.status === 404) {
        setErrorMessage(["account", ""]);
        setNotiMessage({ type: "error", message: "" });
      } else if (result.response.status === 500) {
        setNotiMessage({
          type: "info",
          message: "",
        });
      }
      return;
    }
  }

  // KeyDown 
  function handleKeyDown(key) {
    if (key === "Enter") {
      Login(current_page);
    }
  }

  return (
    <div className="login_form">
      <div className="input_group">
        <FormInput
          data={{
            title: "",
            name: "account",
            type: "text",
            placeholder: "",
          }}
          onChange={accountChange}
          onKeyDown={handleKeyDown}
          value={account}
          errmsg={errorMessage}
        />

        <FormInput
          data={{
            title: "",
            name: "password",
            type: "password",
            placeholder: "",
          }}
          onChange={passwordChange}
          onKeyDown={handleKeyDown}
          value={password}
          errmsg={errorMessage}
        />
      </div>
      <button
        className="submit_btn"
        onClick={() => {
          Login();
        }}
      >
        
      </button>

      {current_page === "users" ? (
        <div className="btn_group">
          <NavLink to="/register">
            <button className="link_btn register_btn"></button>
          </NavLink>
          <span></span>
          <NavLink to="/admin_login">
            <button className="link_btn admin_login_btn"></button>
          </NavLink>
        </div>
      ) : (
        <div className="btn_group">
          <NavLink to="/login">
            <button className="link_btn user_login_btn"></button>
          </NavLink>
        </div>
      )}

      {/*  */}
      {is_alert && <Notification />}
    </div>
  );
}

function FormInput({ data, onChange, onKeyDown, value, errmsg }) {
  let input_style = "input"; 
  let message = "";
  if (errmsg[0] === data.name) {
    input_style = "input_error";
    message = <label className="error_message">{errmsg[1]}</label>;
  }

  return (
    <div className="input_div">
      <label htmlFor={data.title}>{data.title}</label>
      <input
        className={input_style}
        id={data.title}
        name={data.name}
        type={data.type}
        placeholder={data.placeholder}
        onChange={(e) => {
          onChange(e.target.value);
        }}
        onKeyDown={(e) => {
          onKeyDown(e.key);
        }}
        value={value}
      />
      {message}
    </div>
  );
}
